.hero__title {
    font-weight: 800;
    font-size: 20px;
    letter-spacing: 0.03em;
    line-height: 130%;
    color: var(--black-color);
    margin-top: 40px;
    margin-bottom: 8px;
    @media screen and (min-width: 768px) {
        font-size: 26px;
        margin-top: 80px;
    }
    @media screen and (min-width: 1366px) {
        font-size: 58px;
    }
}
.hero__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.08em;
    color: var(--gray-color);
    @media screen and (min-width: 1366px) {
        font-size: 20px;
        line-height: 164.9%;
    }
}
.hero__text2 {
    color: var(--black-color);
    font-weight: 800;
}
.hero__text3 {
    color: var(--accent-color);
    font-weight: 800;
   }
.hero__btn {
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    align-items: center;
    text-align: center;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: var(--white-color);
    background-color: var(--accent-color);
    padding: 14px 0 14px 0;
    width: 280px;
    height: 48px;
    border: none;
    margin: 40px auto;
    @media screen and (min-width: 768px) {
        width: 260px;
        margin: 48px auto;
    }
    @media screen and (min-width: 1366px) {
        width: 354px;
        height: 70px;
        padding: 27px 70px;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.14em;
    }

}