.header {
    background-color: var(--white-color);
    padding-top: 20px;
    padding-bottom: 40px;
}



.logo {
    display: block;
    width: 80px;
    font-weight: 800;
    font-size: 17px;
    line-height: 1.23;
    letter-spacing: 0.08em;
    color: var(--black-color);

}

.logo__inner {
    display: flex;
    align-items: center;

}

.logo__icon {
    width: 14.2px;
    height: 19.3px;
}

.logo_text {
    font-weight: 700;
    font-size: 5px;
    line-height: 1.2;
    letter-spacing: 0.18em;
    text-transform: uppercase;
    color: var(--black-color);
}

.logo_span {
    color: var(--accent-color);
}
.logo-footer {
    margin: 0 auto;
    margin-bottom: 20px;
}



.main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.site-nav {
    display: none;
}

.button-call {
    display: none;
}





